.lines {
  margin-top: 50px;
  margin-bottom: 50px;
  display: -webkit-flex;
  display: flex;
}

.lines .blackline {
  width: 50%;
  height: 35px;
  background-color: black;
}

.lines .blackline h3 {
  text-align: left;
  color: white;
  line-height: 0%;
