body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App header {
  z-index: 100;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  background-color: rgba(255, 255, 255, 0.5);
}

.App header .flexerino {
  display: -webkit-flex;
  display: flex;
}

.App header #img {
  margin-top: 10px;
  width: 20%;
  height: 50px;
}

.App header nav {
  margin-top: 10px;
  width: 100%;
  height: 75px;
}

.App header ul {
  list-style: none;
  text-align: left;
  display: inline-block;
}

.App header li {
  display: inline-block;
}

.App header li:nth-child(n+1) {
  margin-left: 20px;
}

.App header a {
  text-transform: uppercase;
  text-decoration: none;
  color: black;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
}

.App header form {
  width: 100%;
  margin-top: 25px;
  display: -webkit-flex;
  display: flex;
}

.App header form input {
  height: 25px;
  width: 250px;
  border: none;
  background-color: #a7a7a7;
  padding: 4px;
}

.App header form button {
  height: 33px;
  border: none;
  padding: 8px;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
}

.App header #login {
  margin-top: 15px;
  display: -webkit-flex;
  display: flex;
}

.App header #login #img2 {
  background-color: black;
  width: 25%;
  height: 30px;
  margin-top: 15px;
}

.App header #login #logintxt {
  margin-top: 15px;
  line-height: 15px;
  background-color: #a7a7a7;
  width: 100%;
  height: 30px;
}

.App header #login #logintxt a {
  margin-left: 5px;
}

main {
  width: 100%;
  height: auto;
}

main .top {
  background-image: url(/img/scuba2.jpg);
  background-position: bottom;
  width: 100%;
  height: 750px;
  display: grid;
  grid-template-columns: 20% 60% 25%;
  grid-template-rows: 40% 25% 25% 25%;
}

main .top .welcomebox {
  background-color: rgba(255, 255, 255, 0.5);
  width: 100%;
  -ms-grid-column: 2;
  -ms-grid-column-span: 1;
  grid-column: 2/3;
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  grid-row: 2/3;
}

main .top .welcomebox h2, main .top .welcomebox h3 {
  padding-top: 25px;
  text-transform: uppercase;
  text-align: center;
  font-weight: 440;
}

main .dykkerudstyr {
  margin: 0 10%;
}

main .products {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 50px;
  width: 100%;
}

main .products .container {
  background-color: white;
  margin: 0 50px;
  width: 25%;
  text-align: center;
  box-shadow: 0px 0px 46px 0px rgba(0, 0, 0, 0.35);
}

main .products .container .img {
  width: 75%;
}

main .semerudstyr {
  text-decoration: none;
  color: black;
  font-size: 28px;
  text-transform: uppercase;
}

main .semerudstyr h3 {
  text-align: center;
  font-weight: 220;
}

main .dykudiverden {
  margin: 0 10%;
}

main .dykudiverden .gcontainer {
  width: 50%;
  background-color: black;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
}

main .dykudiverden .gcontainer .imgcontainer {
  background-color: white;
  padding: 50px 50px;
}

main .dykudiverden .gcontainer .imgcontainer img {
  width: 100%;
  height: 250px;
}

main .dykudiverden .gcontainer .blackbox {
  width: 100%;
  height: 100px;
  background-color: black;
}

main .dykudiverden .gcontainer .blackbox h2 {
  color: white;
  margin: 50px 0 0 25px;
  text-transform: uppercase;
}

main .dykudiverden .gcontainer .blackbox p {
  margin: 10px 25px;
  color: white;
}

main .flex {
  display: -webkit-flex;
  display: flex;
}

main .fillout {
  width: 5%;
  height: 100px;
}

main .cerf {
  margin: 0 10%;
}

main .cerf .padibox {
  width: 100%;
  height: auto;
  display: -webkit-flex;
  display: flex;
  border: 2px solid black;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
}

main .cerf .padibox article {
  width: 50%;
  margin: 100px 50px;
}

main .cerf .padibox article h3 {
  font-weight: 550;
}

main .cerf .padibox article p {
  font-weight: 750;
}

main .cerf .padibox .padiboi {
  margin: 50px 50px;
}

footer {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 350px;
  background-color: black;
}

footer div {
  width: 50%;
  margin: 50px;
  margin-left: 10%;
}

footer div ul {
  list-style: none;
  padding: 0;
}

footer div ul li {
  margin-right: 5%;
  margin-bottom: 5px;
}

footer h3 {
  color: white;
}

footer a {
  color: grey;
  text-decoration: none;
  font-weight: 550;
}

footer a:hover {
  color: #b4b3b3;
}
